* {
  box-sizing: border-box;
}

img {
  display: block;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  padding: 8px;
  font-size: 10px;
  scroll-behavior: smooth;
}

.search-form {
  position: fixed;
  top: 0;
  left: 0;

  display: flex;
  justify-content: center;

  background-color: #3563ab;
  font: inherit;
  padding: 8px;
  width: 100vw;
}

.gallery {
  margin-top: 60px;
  display: flex;
  flex-wrap: wrap;
  margin-right: -16px;
}

.gallery__item {
  display: flex;
  flex-direction: column;
  width: calc(25% - 16px);
  margin-right: 16px;
  margin-bottom: 16px;
  box-shadow: 1px 2px 3px 0px rgba(0, 0, 0, 0.61);
  cursor: zoom-in;
}

.gallery__img {
  width: 100%;
  height: 100%;
}

.info {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.info__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
